import React, { Component } from "react";
import { Header, Button } from "semantic-ui-react";
import { Redirect } from "react-router-dom";
import CanvasDraw from "react-canvas-draw";
import axios from "axios";

import "../style/App.css";
import deviceCheck from "../js/deviceCheck"

const API = "https://icem-2020.herokuapp.com";

const device = deviceCheck();

export default class Prepare extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      brushRadius: 5,
      lazyRadius: 0,
      activeItem: "black"
    };
  }

  componentDidMount() {
    document.title = "ICEM 2020 - Whiteboard";
    // check if user already logged in
    // if (localStorage.accessToken) {
    //   this.checkLogin();
    // } else {
    //   console.log("Token not found!");
    //   this.setState({ redirect: "/"})
    // }
  }

  checkLogin() {
    this.setState({ loading: true });
    axios.get(API + "/users/profile").then(res => {
      // console.log(res);
      this.setState({
        username: res.data.id,
        team_name: res.data.team_name,
        university: res.data.university,
        loading: false
      });
    });
  }

  submit() {}

  render() {
    if (this.state.redirect) {
      return <Redirect to={this.state.redirect} />;
    }
    return (
      <div className="App">
        <header className="App-header">
          <div id="header">
            <Header as="h1" icon className="white-txt">
              Time Left: 90 sec
              <Header.Subheader className="white-txt">
                {this.state.university + " @ ICEM 2020"}
              </Header.Subheader>
              <Header.Subheader className="white-txt">
              Use your {device ? "finger" : "mouse"} to draw
              </Header.Subheader>
              
            </Header>
          </div>

          <div className="whiteboard-size">
            <CanvasDraw
              ref={canvasDraw => (this.Canvas = canvasDraw)}
              className="whiteboard-size"
              lazyRadius={this.state.lazyRadius}
              brushColor={this.state.activeColor}
              brushRadius={this.state.brushRadius}
            />
          </div>
          
          <div id="footer">
            <Button
              color="green"
              size="massive"
              onClick={this.Submit}
              disabled={this.state.loading}
              content={this.state.loading ? "Loading..." : "Submit"}
            />
            <Button
              size="big"
              color="red"
              onClick={() => {
                this.Canvas.clear();
              }}
            >
              Clear
            </Button>
            <Button
              size="big"
              color="blue"
              onClick={() => {
                this.Canvas.undo();
              }}
            >
              Undo
            </Button>
          </div>
        </header>
      </div>
    );
  }
}
